import React, { Component } from 'react'
import { Tabs, WhiteSpace,Flex} from 'antd-mobile';
export default class Tabins extends Component {
    renderContent = tab =>
    (<div className="flex-container">
        <WhiteSpace size="lg" />
        <Flex>
            <Flex.Item><img src={require(`./images/灵1.jpg`)} style={{width:'95%',height:'250px'}} alt=""/></Flex.Item>
            <Flex.Item><img src={require(`./images/灵2.jpg`)} style={{width:'95%',height:'250px'}} alt=""/></Flex.Item>
        </Flex>
        <WhiteSpace size="lg" />
        <Flex>
            <Flex.Item><img src={require(`./images/灵3.jpg`)} style={{width:'95%',height:'250px'}} alt=""/></Flex.Item>
            <Flex.Item><img src={require(`./images/灵4.jpg`)} style={{width:'95%',height:'250px'}} alt=""/></Flex.Item>
        </Flex>
        <WhiteSpace size="lg" />
        <Flex>
            <Flex.Item><img src={require(`./images/灵1.jpg`)} style={{width:'95%',height:'250px'}} alt=""/></Flex.Item>
            <Flex.Item><img src={require(`./images/灵2.jpg`)} style={{width:'95%',height:'250px'}} alt=""/></Flex.Item>
        </Flex>
    </div>);

  render() {
    const tabs = [
      { title: '推荐' },
      { title: '冬季' },
      { title: '宜家' },
      { title: '小清新' },
      { title: '小户型' },
      { title: '个性色彩' },
      { title: '美丽' },
    ];

    return (
      <div>
        <WhiteSpace />
        <Tabs tabs={tabs} renderTabBar={props => <Tabs.DefaultTabBar {...props} page={5} />}>
          {this.renderContent}
        </Tabs>
        <WhiteSpace />
      </div>
    );
  }
}
