import React, { Component } from 'react'
import { NavBar,Carousel,SearchBar,Flex,WhiteSpace} from 'antd-mobile';
export default class Shopping extends Component {
    state = {
        data: ['1', '2'],
        imgHeight: 176,
    }
    componentDidMount() {
      // simulate img loading
      setTimeout(() => {
        this.setState({
          data: ['1', '2'],
        });
      }, 100);
    }
    render() {
        return (
            <div style={{position:'relative'}}>
                 <NavBar style={{backgroundColor:'#3fcccb'}}>商城</NavBar>
                 <SearchBar placeholder="输入关键字搜索" maxLength={8} 
                 style={{position:'absolute',top:'40px',backgroundColor:'#282828',width:'100%',zIndex:'100',opacity:'70%'}} />
                 <Carousel
                    autoplay={true} >
                    {this.state.data.map(val => (
                        <img
                            key={val}
                            src={require(`./images/haowu${val}.jpg`)}
                            alt=""
                            style={{ width: '100%', verticalAlign: 'top' ,height:'270px'}}
                            onLoad={() => {
                            // fire window resize event to change height
                            window.dispatchEvent(new Event('resize'));
                            this.setState({ imgHeight: 'auto' });
                            }}
                        />
                    ))}
                </Carousel>
                <div  className='flex-container' style={{margin:'0 auto',backgroundColor:'#ffffff'}}>
                    <WhiteSpace size="lg" />
                    <Flex style={{margin:'0 15px'}}>
                        <Flex.Item><img src={require(`./images/1.jpg`)} style={{width:'70%',height:'50px'}} alt=""/><p style={{marginLeft:'20px'}}>桌</p></Flex.Item>
                        <Flex.Item><img src={require(`./images/2.jpg`)} style={{width:'70%',height:'50px'}} alt=""/><p style={{marginLeft:'20px'}}>床</p></Flex.Item>
                        <Flex.Item><img src={require(`./images/3.jpg`)} style={{width:'70%',height:'50px'}} alt=""/><p style={{marginLeft:'20px'}}>椅</p></Flex.Item>
                        <Flex.Item><img src={require(`./images/4.jpg`)} style={{width:'70%',height:'50px'}} alt=""/><p style={{marginLeft:'20px'}}>几</p></Flex.Item>
                        <Flex.Item><img src={require(`./images/5.jpg`)} style={{width:'70%',height:'50px'}} alt=""/><p style={{marginLeft:'20px'}}>柜</p></Flex.Item>
                    </Flex>
                    <Flex style={{margin:'0 15px'}}>
                        <Flex.Item><img src={require(`./images/6.jpg`)} style={{width:'70%',height:'50px'}} alt=""/><p style={{marginLeft:'10px'}}>书架</p></Flex.Item>
                        <Flex.Item><img src={require(`./images/7.jpg`)} style={{width:'70%',height:'50px'}} alt=""/><p style={{marginLeft:'10px'}}>沙发</p></Flex.Item>
                        <Flex.Item><img src={require(`./images/8.jpg`)} style={{width:'70%',height:'50px'}} alt=""/><p style={{marginLeft:'10px'}}>家居饰品</p></Flex.Item>
                        <Flex.Item><img src={require(`./images/9.jpg`)} style={{width:'70%',height:'50px'}} alt=""/><p style={{marginLeft:'5px'}}>户外家居</p></Flex.Item>
                        <Flex.Item><img src={require(`./images/10.jpg`)} style={{width:'70%',height:'50px'}} alt=""/><p style={{marginLeft:'5px'}}>全部分类</p></Flex.Item>
                    </Flex>  
                </div>
                <div className="flex-container">
                    <WhiteSpace size="lg" />
                    <Flex> 
                        <Flex.Item><img src={require(`./images/26.jpg`)} style={{width:'100%',height:'140px'}} alt=""/></Flex.Item>
                        <Flex.Item><img src={require(`./images/27.jpg`)} style={{width:'100%',height:'140px'}} alt=""/></Flex.Item>
                    </Flex>
                    <Flex> 
                        <Flex.Item><p style={{color:'#b7b7b7'}}>Top Art Studio 欧式风格精细</p><p>￥39.95</p></Flex.Item>
                        <Flex.Item><p style={{color:'#b7b7b7'}}>顺顺工艺欧式风格塑料外框</p><p>￥39.95</p></Flex.Item>
                    </Flex>
                </div>
            </div>
        )
    }
}
