import React, { Component } from 'react'
import {NavBar,Carousel,Flex,WhiteSpace,List} from 'antd-mobile'
const Item = List.Item;
export default class Apphome extends Component {
    state = {
        data: ['1', '2', '3'],
        imgHeight: 176,
      }
      componentDidMount() {
        // simulate img loading
        setTimeout(() => {
          this.setState({
            data: ['AiyWuByWklrrUDlFignR', 'TekJlZRVCjLFexlOCuWn', 'IJOtIlfsYdTyaDTRVrLI'],
          });
        }, 100);
      }
    render() {
        return (
            <div>
                <NavBar style={{backgroundColor:'#3fcccb'}}>住吧家居</NavBar>
                
                <Carousel
                    autoplay={true}
                    // infinite
                    // beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
                    // afterChange={index => console.log('slide to', index)}
                    >
                    {this.state.data.map(val => (
                        <img
                            key={val}
                            src={require(`./images/住1.jpg`)}
                            alt=""
                            style={{ width: '100%', verticalAlign: 'top' }}
                            onLoad={() => {
                            // fire window resize event to change height
                            window.dispatchEvent(new Event('resize'));
                            this.setState({ imgHeight: 'auto' });
                            }}
                        />
                    ))}
                </Carousel>
                <div className="flex-container">
                    <WhiteSpace size="lg" />
                    <Flex>
                        <Flex.Item><img src={require(`./images/住2.jpg`)} style={{width:'100%',height:'140px'}} alt=""/></Flex.Item>
                        <Flex.Item><img src={require(`./images/住3.jpg`)} style={{width:'100%',height:'140px'}} alt=""/></Flex.Item>
                        <Flex.Item><img src={require(`./images/住4.jpg`)} style={{width:'100%',height:'140px'}} alt=""/></Flex.Item>
                    </Flex>
                    <WhiteSpace size="lg" />
                </div>
                <List className="my-list">
                    <Item style={{paddingLeft:'0px'}}><div style={{width:'5px',height:'30px',float:'left',backgroundColor:'#3fcccb'}}></div>&nbsp;热门推荐</Item>
                </List>
                <img alt='' src={require(`./images/住6.jpg`)} style={{width:'100%'}}></img>
            </div>
        )
    }
}
