import React, { Component } from 'react'
import {TabBar} from 'antd-mobile'
import Apphome from './Apphome'
import Inspiration from './Inspiration'
import Shopping from './Shopping'
export default class Apptab extends Component {
    constructor(props) {
        super(props);
        this.state = {
          selectedTab: 'blueTab',
        };
      }
      render() {
        return (
          <div style={{position: 'fixed', height: '100%', width: '100%', top: 0 }}>
            <TabBar
              unselectedTintColor="#949494"
              tintColor="#33A3F4"
              barTintColor="white"
              hidden={this.state.hidden}
            >
              <TabBar.Item
                title="首页"
                key="Life"
                icon={<div style={{
                  width: '22px',
                  height: '22px',
                  backgroundImage: `url(${require("./images/shouye.png")})`,
                  backgroundSize:'21px 21px' ,
                  backgroundPosition:'center center'}}
                />
                }
                selectedIcon={<div style={{
                  width: '22px',
                  height: '22px',
                  background: `url(${require("./images/shouye2.png")}) center center /  21px 21px no-repeat` }}
                />
                }
                selected={this.state.selectedTab === 'blueTab'}
                // badge={1}
                onPress={() => {
                  this.setState({
                    selectedTab: 'blueTab',
                  });
                }}
                data-seed="logId"
              >
              <Apphome/>
              </TabBar.Item>
              <TabBar.Item
                icon={
                  <div style={{
                    width: '22px',
                    height: '22px',
                    background: `url(${require("./images/dengpao.png")}) center center /  21px 21px no-repeat` }}
                  />
                }
                selectedIcon={
                  <div style={{
                    width: '22px',
                    height: '22px',
                    background: `url(${require("./images/dengpao2.png")}) center center /  21px 21px no-repeat` }}
                  />
                }
                title="灵感"
                key="Koubei"
                // badge={'new'}
                selected={this.state.selectedTab === 'redTab'}
                onPress={() => {
                  this.setState({
                    selectedTab: 'redTab',
                  });
                }}
                data-seed="logId1"
              >
              <Inspiration/>
                {/* {this.renderContent('Koubei')} */}
              </TabBar.Item>
              <TabBar.Item
                icon={
                  <div style={{
                    width: '22px',
                    height: '22px',
                    background: `url(${require("./images/store_icon.png")}) center center /  21px 21px no-repeat`}}
                  />
                }
                selectedIcon={
                  <div style={{
                    width: '22px',
                    height: '22px',
                    background: `url(${require("./images/store2.png")}) center center /  21px 21px no-repeat`}}
                  />
                }
                title="商城"
                key="Friend"
                // dot
                selected={this.state.selectedTab === 'greenTab'}
                onPress={() => {
                  this.setState({
                    selectedTab: 'greenTab',
                  });
                }}
              >
                <Shopping/>
                {/* {this.renderContent('Friend')} */}
              </TabBar.Item>
              <TabBar.Item
                icon={
                  <div style={{
                    width: '22px',
                    height: '22px',
                    background: `url(${require("./images/wode.png")}) center center /  21px 21px no-repeat`}}
                  />
                }
                selectedIcon={
                  <div style={{
                    width: '22px',
                    height: '22px',
                    background: `url(${require("./images/wode2.png")}) center center /  21px 21px no-repeat`}}
                  />
                }
                title="我的"
                key="my"
                selected={this.state.selectedTab === 'yellowTab'}
                onPress={() => {
                  this.setState({
                    selectedTab: 'yellowTab',
                  });
                }}
              >
                {/* {this.renderContent('My')} */}
              </TabBar.Item>
            </TabBar>
          </div>
        );
      }
}
